@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Regular.woff2"), url("../fonts/Montserrat-Regular.woff");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Bold.woff2"), url("../fonts/Montserrat-Bold.woff");
  font-weight: 700;
  font-style: normal;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-ExtraBold.woff2"), url("../fonts/Montserrat-ExtraBold.woff");
  font-weight: 800;
  font-style: normal;
}
* {
  box-sizing: border-box;
  outline: none;
}
a {
  text-decoration: none;
}
h2 {
  font-family: 'Montserrat', Arial, sans-serif;
  font-style: normal;
  font-weight: 800;
  font-size: 36px;
  line-height: 125%;
  letter-spacing: 0.03em;
  color: #202020;
  margin: 0;
}
input[type="checkbox"],
input[type="file"] {
  display: none;
}
input[type="checkbox"] + label {
  position: relative;
}
input[type="checkbox"] + label::before {
  content: '';
  display: block;
  width: 20px;
  height: 20px;
  background: url('../img/svg/check-off.svg') no-repeat;
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
}
input[type="checkbox"]:checked + label::before {
  background: url('../img/svg/check-on.svg') no-repeat;
  position: absolute;
  left: 0;
}
::-webkit-scrollbar {
  display: none;
}
.start-btn {
  min-width: 230px;
  position: relative;
  padding: 10px 80px 10px 43px;
  text-align: left;
  font-family: 'Montserrat', Arial, sans-serif;
  font-style: normal;
  font-weight: 800;
  font-size: 14px;
  line-height: 17px;
  letter-spacing: 0.15em;
  color: #FAFAFA;
  background: linear-gradient(90deg, #0487D9 0%, #049DD9 100%);
  border-radius: 100px;
  border: 3px solid transparent;
  transition: all 0.3s ease;
  cursor: pointer;
}
.start-btn-arrow {
  display: block;
  width: 48px;
  height: 8px;
  position: absolute;
  top: 50%;
  right: 30px;
  transform: translateY(-50%);
}
.start-btn:hover,
.start-btn:focus {
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.2);
  transition: all 0.3s ease;
}
.start-btn:active {
  border: 3px solid #049DD9;
  color: #474747;
  background: #FFFFFF;
  transition: all 0.3s ease;
}
.start-btn-arrow {
  fill: #FAFAFA;
  transition: all 0.3s ease;
}
.start-btn:active .start-btn-arrow {
  fill: #474747;
  transition: all 0.3s ease;
}
.header {
  display: flex;
  align-items: center;
  height: 112px;
  background-color: #FFFFFF;
}
.header__main-wrap {
  display: flex;
  justify-content: space-between;
  width: 100%;
}
.header__left-wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 50%;
  min-width: 640px;
}
.header__right-wrap {
  display: flex;
  align-items: center;
}
.header__phone-block {
  display: flex;
  flex-direction: column;
  text-align: left;
}
.header__phone {
  font-family: 'Montserrat', Arial, sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 22px;
  letter-spacing: 0.05em;
  color: #202020;
  transition: all 0.3s ease;
  cursor: pointer;
}
.header__phone:hover,
.header__phone:focus {
  color: #0487D9;
  transition: all 0.3s ease;
}
.header__start-btn {
  position: relative;
  min-width: 212px;
  height: 29px;
  padding: 5px 20px 5px 35px;
  border: none;
  border-left: 1px solid #0487D9;
  border-right: 1px solid #0487D9;
  background: #FFFFFF;
  font-family: 'Montserrat', Arial, sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 22px;
  letter-spacing: 0.05em;
  color: #0487D9;
  transition: all 0.3s ease;
  cursor: pointer;
}
.header__start-btn-plus {
  width: 9px;
  height: 11px;
  position: absolute;
  left: 15px;
  top: 50%;
  transform: translateY(-30%);
  fill: #0487D9;
  transition: all 0.3s ease;
}
.header__start-btn:hover,
.header__start-btn:focus {
  color: #474747;
  border-left: 1px solid #474747;
  border-right: 1px solid #474747;
  transition: all 0.3s ease;
}
.header__start-btn:hover .header__start-btn-plus,
.header__start-btn:focus .header__start-btn-plus {
  fill: #474747;
  transition: all 0.3s ease;
}
.header__lang-btn {
  display: none;
  width: 55px;
  padding-left: 30px;
  position: relative;
  border: none;
  background: #FFFFFF;
  font-family: 'Montserrat', Arial, sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 22px;
  letter-spacing: 0.05em;
  text-transform: capitalize;
  color: #202020;
  transition: all 0.3s ease;
  cursor: pointer;
}
.header__lang-btn:hover,
.header__lang-btn:focus {
  opacity: 0.6;
  transition: all 0.3s ease;
}
.header__lang-btn-icon {
  display: block;
  width: 20px;
  height: 20px;
  position: absolute;
  left: 0;
}
.header__lang-btn.active-lang {
  display: flex;
}
.container {
  width: 95%;
  padding: 0 50px;
  margin: 0 auto;
  max-width: 1850px;
}
.greetings {
  min-height: 350px;
  height: calc(100vh - 112px);
}
.greetings__wrap {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100%;
  position: relative;
  transition: all 1.5s ease;
}
.greetings__background-wrap {
  height: 100%;
  background: url('../img/png/mockup-mobile.png') no-repeat 300% 1000%, url('../img/png/mockup-pen.png') no-repeat -200% -2000%, url('../img/png/mockup-tablet.png') no-repeat 1000% 70%, url('../img/png/mockup-mobile2.png') no-repeat -1170% -4000%;
}
.greetings__background-wrap.onload {
  background: url('../img/png/mockup-mobile.png') no-repeat 80% 15%, url('../img/png/mockup-pen.png') no-repeat 90% 60%, url('../img/png/mockup-tablet.png') no-repeat 82% 50%, url('../img/png/mockup-mobile2.png') no-repeat 70% 90%;
  transition: all 1.5s ease;
}
.greetings__desc-block {
  padding: 35px 0 0;
  max-width: 510px;
  position: relative;
}
.greetings__desc-block::before {
  content: '';
  display: block;
  width: 67px;
  height: 5px;
  position: absolute;
  left: -1000px;
  top: 0;
  background: #0487D9;
  transition: all 0.3s ease;
}
.greetings__desc-block.onload::before {
  left: 0;
  transition: all 0.3s ease;
}
.greetings__text {
  margin: 0;
  font-family: 'Montserrat', Arial, sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 125.9%;
  letter-spacing: 0.03em;
  text-transform: uppercase;
  color: #202020;
  transform: translateY(100px);
  opacity: 0;
  transition: all 0.5s ease;
}
.greetings__text.onload {
  opacity: 0.7;
  transform: translateY(0);
  transition: all 0.5s ease;
}
.greetings__title {
  font-family: 'Montserrat', Arial, sans-serif;
  font-style: normal;
  font-weight: 800;
  font-size: 48px;
  line-height: 135%;
  letter-spacing: 0.03em;
  text-transform: uppercase;
  transform: translateY(100px);
  opacity: 0;
  transition: all 0.5s ease;
}
.greetings__title.onload {
  transform: translateY(0px);
  opacity: 1;
  transition: all 0.5s ease;
}
.greetings__start-btn {
  transform: translateY(100px);
  opacity: 0;
  transition: all 0.5s ease;
}
.greetings__start-btn.onload {
  transform: translateY(0px);
  opacity: 1;
  transition: all 0.5s ease;
}
.greetings__title-accent {
  color: #0487D9;
}
.greetings__pic-wrap {
  height: 90%;
  max-height: 550px;
  width: 50%;
}
.greetings__mouse-icon-wrap {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 64px;
  height: 64px;
  padding: 0;
  position: absolute;
  left: 50%;
  bottom: 15px;
  transform: translateX(-50%);
  list-style: none;
  background-color: #FFFFFF;
  border-radius: 50%;
}
.greetings__mouse-icon {
  fill: #000;
}
.greetings__mouse-icon circle {
  animation: scroll 1.5s ease infinite;
}
.greetings__pic-wrap {
  position: relative;
}
@keyframes scroll {
  0% {
    opacity: 0;
    cy: 231;
  }
  33% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    cy: 481;
  }
}
.contact-form__wrap.opened-form {
  transform: translateX(0);
}
.contact-form__wrap {
  position: fixed;
  left: 0;
  bottom: 0;
  height: calc(100vh - 112px);
  overflow-y: scroll;
  width: 100%;
  padding: 30px 0;
  background: #f8f8f8;
  z-index: 5;
  transform: translateX(-120%);
  transition: transform 0.5s ease;
}
.contact-form__wrap .container {
  min-height: 100%;
  display: flex;
  align-items: center;
  position: relative;
}
.contact-form {
  width: 55%;
  min-height: 497px;
}
.contact-form__desc {
  font-family: 'Montserrat', Arial, sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 180%;
  letter-spacing: 0.03em;
  color: #474747;
}
.contact-form__input-wrap {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.contact-form__text-input {
  width: 47%;
  background: transparent;
  border: none;
  border-bottom: 1px solid #474747;
  padding-bottom: 5px;
  margin-top: 45px;
}
.contact-form__text-input::placeholder {
  font-family: 'Montserrat', Arial, sans-serif;
  font-weight: 400;
  font-size: 15px;
  letter-spacing: 0.03em;
  color: #474747;
  opacity: 0.7;
}
.contact-form__label {
  display: flex;
  width: 61%;
  padding-left: 30px;
  margin: 35px 0 40px 0;
  font-family: 'Montserrat', Arial, sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 180%;
  letter-spacing: 0.03em;
  color: #474747;
  cursor: pointer;
}
.close-btn {
  width: 25px;
  height: 25px;
  padding: 0;
  border: 0;
  background: none;
  position: absolute;
  right: 50px;
  top: 0;
  fill: #474747;
  cursor: pointer;
  transition: all 0.3s ease;
}
.close-btn:hover,
.close-btn:focus {
  opacity: 0.6;
  transform: rotate(90deg);
  transition: all 0.3s ease;
}
.close-btn svg {
  width: 25px;
  height: 25px;
}
.contact-form__interactive-wrap {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.contact-form__file-wrap {
  width: 30%;
  position: relative;
  padding-left: 35px;
  cursor: pointer;
}
.contact-form__file-wrap:hover,
.contact-form__file-wrap:focus {
  opacity: 0.6;
  transition: all 0.3s ease;
}
.contact-form__file-label {
  display: block;
  max-width: 100%;
  font-family: 'Montserrat', Arial, sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 160%;
  letter-spacing: 0.025em;
  text-decoration-line: underline;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  opacity: 0.8;
  cursor: pointer;
}
.contact-form__paperclip {
  width: 25px;
  height: 25px;
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
}
